<template>
	<a-table :dataSource="data" :columns="columns" :loading="loading">
		<span slot="action" slot-scope="record">
			<a href="javascript:;">更新</a>
			<a-divider type="vertical" />
			<a href="javascript:;" @click="removeadd(record)">删除</a>
			<a-divider type="vertical" />
		</span>
	</a-table>
</template>

<script>
	const columns = [{
			title: '姓名',
			dataIndex: 'name',
			key: 'name',
		},
		{
			title: '创建时间',
			dataIndex: 'crtm',
			key: 'crtm',
		},
		{
			title: '邮箱',
			dataIndex: 'email',
			key: 'email',
		},
		{
			title: '电话',
			key: 'phone',
			dataIndex: 'phone',
		},
		{
			title: 'Action',
			key: 'action',
			scopedSlots: {
				customRender: 'action'
			},
		},
	];
	import * as api from "@/api/index.js"
	export default {
		created() {
			this.getList()
		},
		data() {
			return {
				data: [],
				columns,
				loading: false
			};
		},
		methods: {
			async getList() { //获取用户列表
				let that = this
				let res = await that.APIlist()
				if (res.data.code === 1000) {
					let list = res.data.data.records
					list = list.map((item) => {
						return item = Object.assign({}, item, {
							"key": item.id
						})
					})
					that.data = list
				}
			},
			async removeadd(record) { //删除用户
				let that = this
				that.$confirm({
					title: '是否删除用户',
					content: '注意，删除之后无法恢复',
					async onOk() {
						that.loading = true
						let res = await that.APIremo(record)
						if (res.data.code === 1000) {
							that.getList()
							that.loading = false
							that.$message.success("删除成功")
						}
					},
				});

				window.console.log(123)
			},
			async APIlist() { //获取用户接口
				let res = await api.list({
					current: 1,
					"size": 10
				})
				return res
			},
			async APIremo(record) { //删除接口
				let res = await api.deleteUser([record.id])
				return res
			}
		}
	};
</script>

